<template>
<div class="ads-picture">
    <div class="ads-picture-link">
        <at-input v-model="link" placeholder="http://"></at-input>
    </div>
	<div class="pic mt10">
 	 	<at-progress v-if="percent.show" :percent="percent.val"></at-progress>
 	 	<div v-if="imageSrc.show">{{imageSrc.text}}</div>
 	 </div>
    <div class="goodsPicture mt10">
 	 	 <vue-core-image-upload
		  :class="['btn', 'btn-primary']"
		  :crop="true"
		  crop-ratio="2:1"
		  @imagechanged="imagechanged"
		  @imageuploading="imageuploading"
		  @imageuploaded="imageuploaded"
		  @errorhandle="errorhandle"
		  :data="imageData"
		  text="+"
		  :max-file-size="5242880"
		  url="http://cys520.cn:3000/api/upload/goodsPicture" >
		</vue-core-image-upload>
 	 </div>
 	 <div class="btn-submit mt10">
 	 	<at-button type="primary" @click="inserAdsPicture">创建商品</at-button>
 	 </div>
    <div class="piclist mt10">
        <el-table height=320
	    :data="AdsPicData"
	    border
	    style="width: 100%">

	    <el-table-column
	      prop="creat_date"
	      label="日期"
	      >
	    </el-table-column>
	    <el-table-column
	      prop="pictureUrl"
	      label="图片地址"
	      width="120">
	    </el-table-column>
	    <el-table-column
	      prop="link"
	      label="图片链接"
	      width="120">
	    </el-table-column>
	    <el-table-column
	      prop="sort"
	      label="图片排序"
	      width="100">
	    </el-table-column>
	    <el-table-column
	      label="是否展示"
	      width="100">
		   <template scope="scope">
			   <el-button type="primary" size="mini">{{scope.row.show }}</el-button>
		   </template>
	    </el-table-column>
	    <el-table-column
	      fixed="right"
	      label="操作"
	      width="100">
	      <template scope="scope">
	        <el-button type="text" size="small" @click="DeletAdsPicture($event,scope.$index,AdsPicData)">删除</el-button>
	        <el-button type="text" size="small" @click="UpdateAdsPicture($event,scope.$index,AdsPicData)">设置</el-button>
	      </template>
	    </el-table-column>
	</el-table>
    </div>
	<at-modal width="320" v-model="ModleShow" title="修改广告图" @on-confirm="handleConfirm" @on-cancel="handleCancel">
		 <div class="ly-ads">
			 <at-input v-model="link" placeholder="http://"></at-input>
			 <div class="ly-ads-show">
				  展示 ： <el-switch
					v-model="open"
					on-text=""
					off-text="">
				  </el-switch>
			 </div>
			 <div class="ly-sort"><el-input-number v-model="sort" :step="1"  :min="1"></el-input-number></div>
			 <div class="btn-submit mt10">
				<at-button type="primary" @click="updateAdsPictureSubmit">提交</at-button>
			 </div>
		 </div>
		 
	     <div slot="footer"></div>
	</at-modal>
</div>
	
</template>
<script>
import VueCoreImageUpload from "vue-core-image-upload";
export default {
  data() {
    return {
      link: "",
			pid: "",
			sid : 0,
      ModleShow: false,
      open: false,
      sort: 1,
      imageSrc: {
        url: "",
        text: "",
        show: false
      },
      percent: {
        val: 0,
        show: false
      },
      imageData: {
        text: ""
      },
      AdsPicData: []
    };
  },
  created() {
    let self = this;
    this.$store.dispatch("getAdsPictureList", {
      cb: function(data) {
        self.AdsPicData = data.adsPic;
      }
    });
  },
  methods: {
    imagechanged() {},
    imageuploading(flie) {
      this.percent.show = true;
      this.percent.val = 0;
    },
    imageuploaded(res) {
      if (res.code == 200) {
        this.percent.val = 100;
        this.imageSrc.show = true;
        this.imageSrc.text = "图片上传成功";
        this.imageSrc.url = res.data.smallUrl;
      } else {
        this.percent.show = false;
        this.percent.val = 0;
      }
    },
    errorhandle(err) {
      this.percent.show = false;
      this.percent.val = 0;
    },
    inserAdsPicture() {
      var self = this;
      this.$store.dispatch("inserAdsPicture", {
        link: this.link,
        pictureUrl: this.imageSrc.url.replace(/small/, "biger"),
        cb: function(data) {
          self.AdsPicData.unshift(data.data);
          self.$Message.warning({ message: data.Notifi });
        }
      });
    },
    DeletAdsPicture(e, index, row) {
      e.cancelBubble = true;

      let id = row[index]._id;
      let self = this;
      this.$store.dispatch("deletAdsPicture", {
        id: id,
        cb: function(data) {
          row.splice(index, 1);
          self.$Message.warning({ message: data.Notifi });
        }
      });
    },
    UpdateAdsPicture(e, index, row) {
      this.ModleShow = true;
      e.cancelBubble = true;
      this.sid = index;
      this.pid = row[index]._id;
      this.link = row[index].link;
      this.open = row[index].show;
      this.sort = row[index].sort;
    },
    updateAdsPictureSubmit() {
      let self = this;
      this.$store.dispatch("updateAdsPicture", {
        id: this.pid,
        link: this.link,
        show: this.open,
        sort: this.sort,
        cb: function(data) {
					self.AdsPicData[self.sid].link =  self.link
					self.AdsPicData[self.sid].show =  self.open
					self.AdsPicData[self.sid].sort =  self.sort
					self.ModleShow = false;
          self.$Message.warning({ message: data.Notifi });
        }
      });
    },
    handleConfirm() {},
    handleCancel() {}
  },
  components: {
    VueCoreImageUpload: VueCoreImageUpload
  }
};
</script>
<style>
.ads-picture {
  padding: 10px 20px 0;
}
.ly-ads-show {
  margin-top: 10px;
}
.ly-sort {
  margin-top: 10px;
}

</style>




