<template>
  <div class="front-acticle-details">
      <div class="details-line"><span>商品信息</span></div>
      <div class="content" v-html="content"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      content: ""
    };
  },
  created() {
    let id = this.$route.path.replace(/\/article\//, ""),
      self = this;
    this.$store.dispatch("GetarticleDetails", {
      id: id,
      cb: function(data) {
        if (data.data && data.data.content) {
          self.content = data.data.content;
        }
      }
    });
  }
};
</script>
<style>
.front-acticle-details{

}
.front-acticle-details .content{ margin: 0 10px 15px;}
.front-acticle-details .content img{ width: 100%;}
.details-line {
  color: #9a9a9a;
  position: relative;
  height: 24px;
  line-height: 24px;
  margin: 10px 0;
}
.details-line::before {
  content: "";
  position: absolute;
  height: 1px;
  background: #f1f1f1;
  width: 100%;
  top: 12px;
}
.details-line span {
  width: 100px;
  margin: 0 auto;
  background: #ffffff;
  display: block;
  height: 24px;
  text-align: center;
  position: relative;
  z-index: 1;
}
</style>




