<template>
 <div class="goods-add">

 	 <el-select v-model="typeValue" placeholder="选择类别">
 	     <el-option
 	       v-for="(item,index) in tyeList"
 	       :key="index"
 	       :label="item"
 	       :value="item">
 	     </el-option>
 	 </el-select>
 	 <div class="goodsName mt10">
 	     <at-input v-model="goodsName" placeholder="输入商品名称"></at-input>
 	 </div>
 	 <div class="goodsPrice mt10">
 	 	 <!-- <at-input-number @change="changePrice" v-model="goodsPrice" :step="0.3" :min="1"></at-input-number> -->
 	 	 <at-input-number v-model="goodsPrice" @blur="changePriceByBur" @change="changePrice" :step="1" :min="1"></at-input-number>
 	 </div>
 	 
 	 <div class="goodsDesc mt10">
 	 	<at-textarea v-model="goodsDesc" placeholder="输入商品介绍"></at-textarea>
 	 </div>
 	 <div class="pic mt10">
 	 	<at-progress v-if="percent.show" :percent="percent.val"></at-progress>
 	 	<div v-if="imageSrc.show">{{imageSrc.text}}</div>
 	 </div>
 	 <div class="goodsPicture mt10">
 	 	 <vue-core-image-upload
		  :class="['btn', 'btn-primary']"
		  :crop="true"
		  crop-ratio="1:1"
		  @imagechanged="imagechanged"
		  @imageuploading="imageuploading"
		  @imageuploaded="imageuploaded"
		  @errorhandle="errorhandle"
		  :data="imageData"
		  text="+"
		  :max-file-size="5242880"
		  url="http://cys520.cn:3000/api/upload/goodsPicture" >
		</vue-core-image-upload>
 	 </div>
 	 <div class="btn-submit mt10">
 	 	<at-button type="primary" @click="inserGoods">创建商品</at-button>
 	 </div>
 </div> 
</template>
<script>
import VueCoreImageUpload from 'vue-core-image-upload'
import { mapActions } from 'vuex'

export default {
   data(){
		return {
			sid : 0,
            typeValue : "",
            goodsName :"",
            goodsDesc : "",
            goodsPrice : 0,
            percent : {
            	val : 0,
                show : false
            },
            imageSrc : {
            	url : '',
            	text : '',
            	show : false
            },
            imageData : {
            	text: ''
            },
            tyeList : [],
            Notifi : {}
		}
	},
	components: {
	    'VueCoreImageUpload': VueCoreImageUpload
	},
	
	mounted(){
		var self = this;
		var html = [];
		this.$store.dispatch('getGoodType',{
            cb : function (data) {   
            	if(data.data.body.code === 200){
            		var arr = [];
            		for (var i = 0; i < data.data.body.data.list.length; i++) {
                       arr[i] = data.data.body.data.list[i].type;
                       
            		}
            	    self.tyeList = arr;
            	   
            	    //self.$refs.atSelect.appendChild('<br>');
            	}
            }
        })
       
	},
	methods: {
		...mapActions([
	      'insertGoodAsync',
	    ]),
	    changeSid(sid,obj){  
            this.sid = sid;
            if(obj){   
	            this.typeValue = obj.type;
	            this.goodsName = obj.name;
	            this.goodsDesc = obj.describe;
	            this.goodsPrice = parseInt(obj.price);
	            this.imageSrc.url = obj.preview;
            }
	    },
		changeType : function (value) {
			//this.typeValue =  value;
			console.log(this.typeValue)
		},
		changePriceByBur:function (e) {
		   	this.goodsPrice = parseInt(e.target.value);
		},
		changePrice : function (e) {
			
		},
		imagechanged() { 
		   
	    },
	    imageuploading(flie) {
	       this.percent.show = true;
           this.percent.val=0;
	    },
	    imageuploaded(res) { console.log(res)
	       if(res.code == 200){
			   this.percent.val = 100;
			   this.imageSrc.show = true;
			   this.imageSrc.text = '图片上传成功'
			   this.imageSrc.url = res.data.smallUrl;
		   }else{
		   	   this.percent.show = false;
		       this.percent.val = 0;
		   }
	    },
	    errorhandle(err) {
	       this.percent.show = false;
		   this.percent.val = 0;
	    },
	    inserGoods : function (){ 
	    	var self = this;
	    	var url = this.imageSrc.url;
	    	if(this.sid){
    	    	this.$store.dispatch('updateGoodAsync',{
    	    		    sid : this.sid,
                        typeValue : this.typeValue,
    		            goodsName : this.goodsName,
    		            goodsDesc : this.goodsDesc,
    		            goodsPrice : this.goodsPrice,
    		            imageSrc : {
    		            	url : url,
    		            	text : '',
    		            	show : false
    		            },
    		            cb : function (notif) { 
    		            	self.$Message.warning({ message: notif.data.data.message })
    		            }
    	    	})
		    }else{
		    	this.$store.dispatch('insertGoodAsync',{
		    		    sid : this.sid,
	                    typeValue : this.typeValue,
			            goodsName : this.goodsName,
			            goodsDesc : this.goodsDesc,
			            goodsPrice : this.goodsPrice,
			            imageSrc : {
			            	url : url,
			            	text : '',
			            	show : false
			            },
			            cb : function (notif) { 
			            	self.$Message.warning({ message: notif.data.data.message })
			            }
		    	})
		    }
	    }
	}
}
</script>
<style>
.goods-add{ padding: 10px 20px 0; }
.mt10{ margin-top: 10px; }
.g-core-image-upload-btn{ width: 60px; height: 60px; font-size: 38px; text-align: center; color: #333; border: 1px solid #C5D9E8; border-radius: 3px; }
.goods-add .el-input__inner{ height: 34px; }
.goods-add .el-select{ width: 100%; }
.pic{ width: 200px; }
</style>


