<template>
<div class="backend-goods-list">
	<el-table height=498
	    :data="tableData"
	    border
	    style="width: 100%">
	    <el-table-column
	      prop="_id"
	      label="商品ID"
	      >
	    </el-table-column>
	    <el-table-column
	      prop="creat_date"
	      label="日期"
	      >
	    </el-table-column>
	    <el-table-column
	      prop="type"
	      label="商品类型"
	      width="120">
	    </el-table-column>
	    <el-table-column
	      prop="price"
	      label="商品价格"
	      width="120">
	    </el-table-column>
	    <el-table-column
	      prop="name"
	      label="商品名称"
	      width="120">
	    </el-table-column>
	    <el-table-column
	      prop="describe"
	      label="商品描述"
	      width="120">
	    </el-table-column>
	    <el-table-column
	      prop="preview"
	      label="图片地址"
	      width="150">
	    </el-table-column>
	    <el-table-column
	      fixed="right"
	      label="操作"
	      width="100">
	      <template scope="scope">
	        <el-button type="text" size="small" @click="DeletGoodById(scope.$index,tableData)">删除</el-button>
	        <el-button type="text" size="small" @click="handleModle1(scope.$index,tableData)">修改</el-button>
					<el-button type="text" size="small"> <router-link class="al" :to="`/backend/details?${scope.row._id}`">添加详情</router-link></el-button>
	      </template>
	    </el-table-column>
	</el-table>
	
	<at-modal width="320" v-model="Modle1" title="这里是标题" @on-confirm="handleConfirm" @on-cancel="handleCancel">
	     <backendgoodsadd ref="c1"></backendgoodsadd>
	     <div slot="footer"></div>
	</at-modal>
</div>	
</template>
<script>
    import backendgoodsadd from './backend-goods-add'
	export default {
      data() {
        return {
          Modle1 : false,
          rid : 0,
          tableData: []
        }
      },
      components: {
	    'backendgoodsadd': backendgoodsadd
	  },
      mounted(){

            var self = this;
	    	this.$store.dispatch('showGoodsList',{
	    		cb : function (data) { 
	    			if(data.body.code === 200){
	    				var arr = data.body.data.list;
	    				for (var i = 0; i < arr.length; i++) {
	    					arr[i].creat_date = arr[i].creat_date.split(' ')[0];
	    					self.tableData[i] = arr[i];
	    				}
	    			   
	    			}
	            	//self.tableData = data.data.data.ta
	            }
	    	})
      },
      methods : {
      	  deleteGoos(){

      	  },
      	  handleModle1(index, rows){
      	  	  var obj = rows[index];
              this.Modle1 = true;
              this.rid = obj._id;
              //this.$emit('triggerChild', {rid : this.rid})
              this.$refs.c1.changeSid(this.rid,obj);
      	  },
      	  handleConfirm(index, rows){ 
              this.Modle1 = false;
              this.rid = 0;
      	  },
      	  handleCancel(index, rows){ 
              this.Modle1 = false;
              this.rid = 0;
              this.$refs.c1.changeSid(this.rid);
      	  },
      	  DeletGoodById(index, rows){ 
      	  	  var self = this;
      	  	  var obj = rows[index];
      	  	  rows.reverse().splice(index,1);

      	  	  this.tableData = rows;
      	  	  this.$store.dispatch('deleteGoods',{
	    		    id : obj._id,
		            cb : function (notif) { 
		            	self.$Message.warning({ message: notif.data.data.message })
		            }
	    	 })
      	  }
      }
    }
</script>
<style>
	.backend-goods-list{ padding: 10px 20px 0; }
</style>{


