<template>
<div class="add_address">
	<div class="header"><i class="el-icon-arrow-left" @click="goBack"></i>添加新地址</div>
	<div class="add_address_main">
		<div class="add_address_input">
			<label>收货人：</label>
			<input type="text" ref="name" :value="name">
		</div>
		<div class="add_address_input">
			<label>手机号码：</label>
			<input type="text" ref="phone" :value="phone">
		</div>
		<div class="add_address_input">
			<label>选择地区：</label>
			<span class="xx":class="{ 'xr': xr}" @click="showCityList"><em>{{cityName}}</em><i class="el-icon-arrow-right"></i></span>
		</div>
		<div class="add_address_input">
			<label>详细地址：</label>
			<input type="text" ref="country" :value="country">
		</div>
	</div>
	<div class="address_save" @click="address_save">保 存</div>
	<vue-area :props-show="show" :props-result="result" v-on:result="areaResult"></vue-area>
</div>	
</template>
<script>
import { tfooter } from "../components/";
import vueArea from "vue-area";
import cookies from "js-cookie";

export default {
  data() {
    return {
      name: "",
      phone: "",
      cityName: "请选择",
      country: "",
      address: "",
      result: null,
      show: false,
      xr: true
    };
  },
  
  methods: {
    showCityList() {
      this.show = true;
    },
    validata() {
      if (this.name == "") {
        this.$Message.warning({ message: "请输入收货人名字" });
        return false;
      }
      if (!/^1[34578]\d{9}$/.test(this.phone)) {
        this.$Message.warning({ message: "请输入正确的手机号码" });
        return false;
      }
      if (this.address == "" || this.cityName == "请选择") {
        this.$Message.warning({ message: "请选择你的详细地址" });
        return false;
      }
      return true;
    },
    address_save() {
      var va = this.validata();
      var self = this;
      if (va) {
        this.$store.dispatch("addressInsert", {
          name: this.name,
          phone: this.phone,
          address: this.address,
          _user: cookies.get("f_userid"),
          cb: function(data) {
            if (data.body.code == 200) {
              self.$Message.warning({
                message: data.body.message,
                duration: 800
              });
              self.$router.push({ path: "/cart" });
            } else {
              self.$Message.warning({
                message: data.body.message,
                duration: 2000
              });
            }
          }
        });
      }
    },
    areaResult(show, result) {
      this.show = show;
      this.result = result;
      if (result) {
        let province = this.result["province"].name;
        let city = this.result["city"].name;
        let area = this.result["area"].name;
        if (city == "市辖区" || city == "县") {
          city = "";
        }
        if (area == "市辖区") {
          area = "";
        }
        this.cityName = province + city + area;
        this.xr = false; 
        this.address = this.cityName + this.country;
      } else {
        this.xr = true;
      }
    },
    goBack() {
      window.history.go(-1);
    }
  },
  components: {
    vueArea
  },
  mounted() {
    var self = this;
    this.$refs.name.oninput = function() {
      self.name = this.value;
    };
    this.$refs.phone.oninput = function() {
      self.phone = this.value;
    };
    this.$refs.country.oninput = function() {
      self.country = this.value;
	  self.address = self.cityName + self.country;
    };
  }
};
</script>
<style>
.add_address {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #f2f2f2;
}
.add_address .header {
  background: #fff;
  height: 48px;
  line-height: 48px;
  color: #333;
  text-align: center;
  font-size: 20px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 10px;
  position: relative;
}
.add_address .header i {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 38px;
  height: 48px;
  line-height: 48px;
}
.add_address .add_address_main {
  background: #fff;
  border-top: 1px solid #ddd;
}
.add_address .add_address_input {
  height: 38px;
  line-height: 38px;
  font-size: 12px;
  border-bottom: 1px solid #e6e6e6;
  margin: 0 10px;
  display: flex;
  position: relative;
}
.add_address .add_address_input label {
  color: #666;
  width: 62px;
  text-align: right;
  margin-right: 5px;
}
.add_address .add_address_input input {
  flex: 1;
  color: #666;
  height: 36px;
  line-height: 36px;
  border: none;
  -webkit-appearance: none;
  outline: none;
}
.add_address .add_address_input .xx {
  color: #666;
  flex: 1;
}
.add_address .add_address_input .xx.xr {
  text-align: right;
}
.add_address .add_address_input .xx em {
  font-style: normal;
}
.add_address .add_address_input .xx i {
  margin-left: 5px;
  margin-top: 12px;
  font-size: 12px;
  float: right;
}
.address_save {
  border-radius: 10px;
  width: 90%;
  margin: 20px auto;
  background: #ff5c5c;
  color: #fff;
  text-align: center;
  height: 42px;
  line-height: 42px;
  text-align: center;
}
</style>


