<template>
<div class="buy-cart">
	<div class="header">购物车 <span class="e-dd" @click="changeCartEdit">{{ace}}</span></div>
	<div class="buy-cart-list">
		<ul>
	  		<li v-for="(item, index) in userBuyGoodItem" :key="index">
	  			<a href="javascrIpt:;">
	  				<div class="dogo" @click="oToGo(item,index)" :class="{ 'active': item.active}"><i :class="{'iconfont al-icon-dagou' : item.active}"></i></div>
		  			<div class="img"><img :src="`${item.preview}`" :alt="item.name"></div>
		  			<div class="info">
		  				<div class="t-n">{{item.name}}</div>
		  				<div class="describe">{{item.describe}}</div>
		  				<div class="price"><span class="sl">￥{{item.price}}</span><span class="sum" v-if="!vs"><el-input-number size="small" v-model="item.num" @change="changeNum" :min="0"></el-input-number></span></div>
		  			</div>

	  			</a>
	  		</li>
	  	</ul>
	</div>
	<div class="amount">
		<div class="amount-mov" v-if="!vs">
			<div class="am-c" :class="{ 'active': allcur}" @click="SCALL">
				<span class="aml"><i :class="{'iconfont al-icon-dagou' : allcur}"></i>全选</span>
				<span class="amr">合计：<em>￥{{total}}</em></span>
			</div>
			<div class="am-b" @click="openModal">提交订单</div>
		</div>
		<div class="amount-mov" v-if="vs">
			<div class="am-c" :class="{ 'active': allcur}" @click="SCALL">
				<span class="aml"><i :class="{'iconfont al-icon-dagou' : allcur}"></i>全选</span>
			</div>
			<div class="am-b" @click="DelOrder">删除</div>
		</div>
	</div>
	<LRModel :LoginShow="LoginShow" :RegShow="RegShow" v-on:ChangeLoginShow="ChangeLoginShow"></LRModel>
	<tfooter></tfooter>
  <at-modal width="300" ref="amd" v-model="showModal" @on-confirm="PutOrder"><div class="corm">请认真审核您的订单，如无误请您点击确定按钮提交您的订单！</div></at-modal>
</div>	
</template>
<script>
import { tfooter, LRModel } from "../components/";
import cookies from "js-cookie";
export default {
  data() {
    return {
      userBuyGoodItem: [],
      num: {},
      orders: {},
      allcur: false,
      total: 0,
      LoginShow: false,
      RegShow: false,
      aid: "",
      ace: "编辑",
      vs: false,
      showModal: false
    };
  },
  beforeCreate() {
    // this.$store.dispatch("RemovCartByTimeout", {
    //   cb: function() {}
    // });
  },
  created() {
    var self = this;
    self.$store.dispatch("getByIdGroup", {
      cid: cookies.get("u_cart_id"),
      cb: function(data) {
        if(data.body.data){
          for (var i = 0; i < data.body.data[0].length; i++) {
            data.body.data[0][i].num = 0;
          }
          self.$store.state.global.count = data.body.len;
          self.userBuyGoodItem = data.body.data[0];
        }else{
          self.$store.state.global.count = 0;
        }
      }
    });
  },
  components: {
    tfooter,
    LRModel
  },
  methods: {
    oToGo(item, index) {
      item.active = !item.active;
      if (item.active) {
        this.orders[index] = item;
      } else {
        delete this.orders[index];
      }
      this.changeNum();
    },
    SCALL() {
      if (!this.allcur) {
        this.allcur = true;
        this.userBuyGoodItem.map(function(item, index) {
          item.active = true;
          return item;
        });
      } else {
        this.allcur = false;
        this.userBuyGoodItem.map(function(item, index) {
          item.active = false;
          return item;
        });
      }
      this.changeNum();
    },
    openModal() {
      //this.showModal = true;
      this.$set(this.$data, "showModal", true);
      this.$nextTick(() => {
        var at = this.$refs.amd.$el.children[1].children[0];
        at.style.top = (window.innerHeight - at.clientHeight) / 2 + "px";
      });
    },
    PutOrder() {
      var self = this;
      var k = false;
      if (cookies.get("f_username")) {
        this.$store.dispatch("getUserAddress", {
          id: cookies.get("f_userid"),
          cb: function(data) {
            console.log("ss", data);
            if (data.body.code == 200) {
              data.body.data.forEach(item => {
                k = false;
                if (item.select) {
                  k = true;
                  self.aid = item._id;
                  return k;
                }
              });

              if (!k) {
                self.$router.push({ path: "/user/address/select" });
              } else {
                var o = [];
                if (JSON.stringify(self.orders) == "{}") {
                  self.$Message.warning({
                    message: "请您勾选您要预约商品",
                    duration: 2000
                  });
                  return false;
                } else {
                  for (var key in self.orders) {
                    if (self.orders[key].num > 0) {
                      o.push(self.orders[key]);
                    }
                  }
                  console.log("vvv", o);
                }
                if (o.length < 1) {
                  self.$Message.warning({
                    message: "请您填写数量",
                    duration: 2000
                  });
                  return false;
                }
                self.$store.dispatch("OrdersInsert", {
                  uid: cookies.get("f_userid"),
                  orders: o,
                  aid: self.aid,
                  totalPrice: self.total,
                  cb: function(data) {
                    self.$Message.warning({
                      message: data.Notifi,
                      duration: 3000
                    });
                  }
                });
              }
            } else {
              self.$router.push({ path: "/user/address" });
            }
          }
        });
      } else {
        this.LoginShow = true;
      }
    },
    totalPrice() {
      let p = 0,
        self = this;
      this.userBuyGoodItem.map(function(item, index) {
        if (item.active) {
          p += item.price * item.num;
        }
        self.total = p;
      });
      this.$forceUpdate();
    },
    ChangeLoginShow(a, b, u) {
      this.LoginShow = a;
      this.RegShow = b;
      // if (u) {
      //   this.user.name = u;
      // }
    },
    changeNum() {
      this.$nextTick(() => {
        this.totalPrice();
      });
    },
    changeCartEdit() {
      this.ace = this.ace == "编辑" ? "完成" : "编辑";
      this.vs = !this.vs;
    },
    DelOrder() {
      let self = this;
      let ids = [];
      let arr = [];
      self.userBuyGoodItem.forEach(function(item, i) {
        if (item.active) {
          ids.push(item._id);
        } else {
          arr.push(item);
        }
      });
      Promise.all([ids]).then(r => {
        this.$store.dispatch("delCartById", {
          cid: cookies.get("u_cart_id"),
          ids: ids,
          cb: function(data) {
            console.log(data);
            self.$Message.warning({ message: data.Notifi });
            self.userBuyGoodItem = arr;
          }
        });
      });
    }
  }
};
</script>
<style>
.buy-cart {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #f7f7f7;
}
.buy-cart .corm {
  padding: 15px 15px;
}
.buy-cart .header {
  position: relative;
}
.buy-cart .e-dd {
  font-size: 14px;
  color: #9c9c9c;
  position: absolute;
  right: 0px;
  width: 68px;
}
.buy-cart-list .dogo {
  width: 30px;
  height: 88px;
  cursor: pointer;
}
.buy-cart-list .dogo i {
  display: block;
  width: 15px;
  height: 15px;
  border: 1px solid #9e9e9e;
  border-radius: 50%;
  margin: 38px auto 0;
  font-size: 10px;
  color: #fff;
  position: relative;
}
.buy-cart-list .dogo i:before {
  top: -1px;
  position: absolute;
}
.buy-cart-list .dogo.active i {
  background: #e93b3d;
}
.buy-cart .header {
  background: #fff;
  height: 42px;
  line-height: 42px;
  text-align: center;
  border-bottom: 1px solid #ededed;
  font-size: 18px;
}
.buy-cart-list {
  flex: 1;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  padding-bottom: 10px;
}
.buy-cart-list ul li {
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  background: #fff;
  margin-top: 10px;
}
.buy-cart-list ul li a {
  margin: 0 0;
  padding: 10px 15px 5px 0px;
  display: flex;
}
.buy-cart-list ul li .img {
  width: 88px;
  margin-right: 10px;
}
.buy-cart-list ul li .img img {
  width: 100%;
}
.buy-cart-list ul li .info {
  flex: 1;
}
.buy-cart-list .t-n {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  color: #333;
  font-size: 16px;
}
.buy-cart-list .describe {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  color: #9c9c9c;
  line-height: 18px;
}
.buy-cart-list .price {
  color: #dd2727;
  font-size: 16px;
  margin-top: 8px;
}
.buy-cart-list .sl {
  float: left;
  line-height: 28px;
}
.buy-cart-list .sum {
  float: right;
}
.buy-cart-list .sum .el-input-number--small {
  width: 86px;
}
.buy-cart-list .el-input-number__decrease,
.buy-cart-list .el-input-number--small .el-input-number__increase {
  width: 24px;
  line-height: 22px;
  font-size: 12px;
}
.buy-cart-list .el-input-number--small .el-input-number__increase {
  width: 24px;
  line-height: 22px;
  font-size: 12px;
}
.buy-cart-list .el-input-number__decrease {
  right: 24px;
  line-height: 22px;
}
.buy-cart-list .el-input-number--small .el-input__inner {
  padding-right: 48px;
  height: 24px;
  line-height: 24px;
}
.amount .amount-mov {
  height: 38px;
  line-height: 38px;
  background: #fff;
  display: flex;
  border-top: 1px solid #ededed;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
}
.amount .am-c {
  flex: 1;
  padding-left: 8px;
  font-size: 12px;
}
.amount .am-c .aml {
  float: left;
  width: 60px;
  cursor: pointer;
}
.amount .am-c .aml i {
  float: left;
  width: 15px;
  height: 15px;
  border: 1px solid #9e9e9e;
  border-radius: 50%;
  margin-top: 10px;
  margin-right: 8px;
  color: #fff;
  font-size: 12px;
  position: relative;
}
.amount .am-c .aml i:before {
  position: absolute;
  left: 0;
  top: -13px;
}
.amount .am-c .amr {
  float: right;
}
.amount .am-c .amr em {
  font-style: normal;
  margin-right: 10px;
  color: #f30505;
}
.amount .am-b {
  width: 86px;
  background: #f30505;
  color: #fff;
  font-size: 14px;
  text-align: center;
}
.amount .am-c.active .aml i {
  background: #e93b3d;
}
</style>


