import { GETCITY } from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'

import Vue from 'vue'
export default {
  async['getCityAsync'] ({ commit, state },config) {
     
    if (!config.city.name) {
      config.city.name = state.name;
      config.city.rN = state.rN;
    }
    let data = await api.getCityListSync(config);
    let lists = data.body.data.data.returnValue;
    for(var key in lists){
       lists[key].sort = key
    }
    config.city.data = lists;
    let city = config.city;
    commit(GETCITY, {city});
    if(config.cb){
       config.cb(lists);
    }   
  }
}



