import { UERSISERT, ADMINLOGIN, CODETOPNG, USERORDERS, GETALLORDERSLIST, DELETORDERSBYID, COMMENTINSERT } from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'
import Vue from 'vue'

const actions = {
    async ['captchas']({ commit, state }, config) {
        const { data } = await api.captchas(config)
        if (data) {
            const obj = {
                data,
                cb: config.cb
            }
            commit(CODETOPNG, obj);

        }
    },
    async ['insertAdminUsers']({ commit, state }, config) {
        const { data } = await api.insertAdminUsers(config)
        if (data) {
            commit(UERSISERT, data);
            config.cb(state);
        }
    },
    async ['insertFrontUsers']({ commit, state }, config) {
        const { data } = await api.insertFrontUsers(config)
        if (data) {
            commit(UERSISERT, data);
            config.cb(state);
        }
    },
    async ['frontUsersLogin']({ commit, state }, config) {
        const { data } = await api.frontUsersLogin(config)
        if (data) {
            commit(ADMINLOGIN, data);
            config.cb(state);
        }
    },
    async ['adminUserLoginAsync']({ commit, state }, config) {
        const { data } = await api.adminUserLoginAsync(config)
        if (data) {
            commit(ADMINLOGIN, data);
            config.cb(state);
        }
    },
    async ['FrontUserLoginAsync']({ commit, state }, config) {
        const { data } = await api.adminUserLoginAsync(config)
        if (data) {
            commit(ADMINLOGIN, data);
            config.cb();
        }
    },
    async ['OrdersInsert']({ commit, state }, config) {
        const { data } = await api.OrdersInsert(config)
        if (data) {
            commit(USERORDERS, data);
            config.cb(state);
        }
    },
    async ['getAllOrders']({ commit, state }, config) {
        const { data } = await api.getAllOrders(config)
        if (data) {
            commit(GETALLORDERSLIST, data);
            config.cb(state.data);
        }
    },
    async ['deletOrders']({ commit, state }, config) {
        const { data } = await api.deletOrders(config)
        if (data) {
            commit(DELETORDERSBYID, data);
            config.cb(state.Notifi);
        }
    },
    async ['CommentInsert']({ commit, state }, config) {
        const { data } = await api.CommentInsert(config)
        if (data) {
            commit(COMMENTINSERT, data);
            config.cb(state);
        }
    },
    async ['CommentsPull']({ commit, state }, config) {
        const { data } = await api.CommentsPull(config)
        if (data) {
            commit(COMMENTINSERT, data);
            config.cb(state);
        }
    }

}

export default actions


