<template>
	<section class="all-goods-list" :id="id">
		  	<ul>
		  		<li v-for="(item, index) in AllGoodsList" :Key="index">
		  			<router-link class="al" :to="`/article/${item._id}`">
			  			<div class="img"><img :src="`${item.preview}`" :alt="item.name"></div>
			  			<div class="info">
			  				<div class="t-n">{{item.name}}</div>
			  				<div class="describe">{{item.describe}}</div>
			  				<div class="price">￥{{item.price}}</div>
			  			</div>
		  			</router-link>
		  		</li>
		  	</ul>
	</section>
</template>
<script>
	export default {
		props : ['id','type'],
		
		data(){
			return {
               AllGoodsList : [],
               page : 1,
               CH : 0,
               SH : 0,
               OP : false,
               N : true
			}
		},
		created(){
	        this.getGoodByType();
	    },
	    methods : {
	    	getGoodByType(){
		        var self = this;
		        this.$store.dispatch('getGoodByTypeIdAsync',{
		          limit : 5,
		          page : this.page,
		          type : this.type,
		          cb : function (data) { 
	                if(data.body.data.list.length > 0){
	                    self.AllGoodsList = self.AllGoodsList.concat(data.body.data.list)
	                    self.$nextTick(()=>{
		                    self.CH = self.$el.clientHeight; 
		    		        self.SH = self.$el.scrollHeight;
		    		        self.OP = false;
		    		        if(data.body.data.hasNext){
		    		           self.N = true;
		    		        }else{
		    		           self.N = false;
		    		        }
	    		        })
                    }
		          }
		        })
	    	}
	    },
	    mounted(){
	    	var self = this;
	    	this.$nextTick(()=>{
		    	this.$el.addEventListener('scroll',function () { 
		    		let t = this.scrollTop; 
		    		if(t > self.SH - self.CH - 20 && !self.OP && self.N){ console.log('ss')
	                     self.OP = true;
	                     self.page++;  
	                     self.getGoodByType()
		    		}
		    	})
	    	})
	    	
	    }
	}
</script>
<style>
.all-goods-list{ height: 100%;  -webkit-overflow-scrolling : touch; overflow: auto; }
.all-goods-list ul li{  border-bottom: 1px solid #ededed;}
.all-goods-list ul li a{ margin: 0 0; padding: 10px 15px 5px; display: flex; } 
.all-goods-list ul li .img{ width: 88px; margin-right: 10px;}
.all-goods-list ul li .img img{ width: 100%; }
.all-goods-list ul li .info{ flex: 1;}
.all-goods-list .t-n{
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
	color: #333;
	font-size: 16px;
}
.all-goods-list .describe{
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	color: #9c9c9c;
	line-height: 18px;
}
.all-goods-list .price{ color: #DD2727; font-size: 16px;}
</style>


