<template>
 <div class="user-create">
 	 <div class="user-name">
 	     <at-input v-model="userName" placeholder="输入用户名称"></at-input>
 	 </div>
 	 <div class="user-name mt10">
 	     <at-input v-model="userPwd" type="password" placeholder="输入用户密码"></at-input>
 	 </div>
 	 <div class="user-email mt10">
 	     <at-input v-model="email" placeholder="输入用户邮箱"></at-input>
 	 </div>
 	 <div class="btn-submit mt10">
 	 	<at-button type="primary" @click="AdminUserCreate">创建用户</at-button>
 	 </div>
 </div> 
</template>
<script>
    import { mapActions,mapState } from 'vuex'
	export default {
		props: [''],
		data(){
			return {
                userName : "",
                userPwd : "",
                email : ""
			}
		},
		components: {
         
		},
		created(){
			
		},
        computed:{
            
        },
		methods:{
        	...mapActions([
    	      'insertAdminUsers',
    	    ]),
    	    AdminUserCreate(){
    	    	var self = this;
    	    	this.$store.dispatch('insertAdminUsers',{
    	    		userName : this.userName,
    	    		userPwd : this.userPwd,
    	    		email : this.email,
    	    		cb : function () {
                        self.userName = "";
                        self.userPwd = "";
                        self.email = "";
    	    			self.$Message.warning({ message: self.$store.state.users.usersNotifi.message })
    	    		}
    	    	})
    	    }
		}
	}
</script>
<style>
.user-create{ padding: 10px 20px 0; }
</style>


