<template>
  <el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column type="expand">
      <template scope="props">
        <el-form label-position="left" class="demo-table-expand" :key="k" v-for="(orders, k) in  props.row.orders">
          <el-form-item v-for="(item, index) in  ordersThead" :key="index" :label="item">
            <span class="e-html">{{getOrdersInfo(orders,index) }} <span v-if="index==2 && !orders.discountShow" @click="changeDiscount(orders,k, props.row)"  class="el-tag e-pr el-tag--primary">设置折扣</span><input placeholder="0-1之间" :data-index="k"  min="0.0"  max="1" ref="enterDiscount" v-if="index==2 && orders.discountShow" type="number" class="e-discount-input"></span>
          </el-form-item>
        </el-form>
        <el-form label-position="left" class="demo-table-expand">
           <el-form-item label="收货人名字">
            <span>{{ props.row.address.name }}</span>
          </el-form-item>
          <el-form-item label="收货人电话">
            <span>{{ props.row.address.phone }}</span>
          </el-form-item>
          <el-form-item label="收货人地址">
            <span>{{props.row.address.address }}</span>
          </el-form-item>
        </el-form>
        <el-form label-position="left" class="demo-table-expand">
           <el-form-item label="总计">
            <span>{{ props.row.totalPrice }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label="日期"
      sortable
      prop="creat_date">
    </el-table-column>
    <el-table-column
      label="订单客户"
      prop="user.username">
    </el-table-column>
    <el-table-column
      label="地址"
      prop="address.address">
    </el-table-column>
     <el-table-column
      label="操作"
      width="70">
      <template scope="scope">
        <el-button @click="delectOrders(scope.row,tableData,scope.$index)" type="text" size="small">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<style>
.demo-table-expand {
  font-size: 0;
  border-bottom: 1px solid #dfe6ec;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 100%;
}
.e-html {
  position: relative;
  display: block;
}
.e-pr {
  float: right;
}
.e-discount-input::-webkit-input-placeholder {
  color: #c0c0c0;
}
.e-discount-input {
  position: absolute;
  right: 0px;
  width: 60px;
  height: 24px;
  line-height: 24px;
  border: 1px solid #bfcbd9;
  border-radius: 5px;
  text-align: center;
  font-size: 12px;
}
.el-message-box {
  width: 320px;
}
</style>

<script>
export default {
  data() {
    return {
      tableData: [],
      ordersThead: ["商品名称", "商品数量", "折扣", "商品单价"],
      discountShow: false
    };
  },
  created() {
    var self = this;
    this.$store.dispatch("getAllOrders", {
      cb: function(data) {
        let el = data[0];
        let totalPrice = 0;
        for (var index = 0; index < el.length; index++) {
          var element = el[index];
          for (var j = 0; j < element.orders.length; j++) {
            var ele = element.orders[j];
            ele.discountShow = false;
            ele.discount = 1;
            totalPrice += ele.discount * ele.num * ele.price;
          }
          element.totalPrice = totalPrice;
        }
        self.tableData = el;
      }
    });
  },
  mounted() {},
  methods: {
    getOrdersInfo(item, i) {
      switch (i) {
        case 0:
          return item.name;
          break;
        case 1:
          return item.num;
          break;
        case 2:
          return item.discount;
          break;
        case 3:
          return item.price;
          break;
      }
    },
    changeDiscount(item, k, pa) {
      item.discountShow = true;
      var self = this;
      this.$nextTick(() => {
        //console.log(this.$refs.enterDiscount)
        let p = 0;
        this.$refs.enterDiscount.forEach(function(item2, i) {
          item2._this = this;
          item2.onblur = function() {
            // console.log(item2.getAttribute('data-index'))
            item2._this.discountShow = false;
            item2._this.discount = this.value;
            self.calculatePrice(pa);
          };
          item2.oninput = function() {
            if (this.value > 1) {
              this.value = 1;
            } else if (this.value < 0) {
              this.value = 0;
            }
          };
        }, item);
      });
    },
    calculatePrice(pa) {
      let totalPrice = 0;
      console.log(pa.orders);
      pa.orders.forEach(function(item2, i) {
        totalPrice += item2.discount * item2.price * item2.num;
      });
      pa.totalPrice = totalPrice;
    },
    delectOrders(item, data, index) {
      let self = this;
      const h = this.$createElement;
      this.$msgbox({
        title: "消息",
        width: 0,
        message: h("p", null, [h("span", null, "你确定也此删除订单吗")]),
        showCancelButton: true,
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        beforeClose: (action, instance, done) => {
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = "执行中...";
            setTimeout(() => {
              done();
              setTimeout(() => {
                instance.confirmButtonLoading = false;
              }, 100);
            }, 100);
          } else {
            done();
          }
        }
      }).then(action => {
        data.splice(index, 1);
        self.$store.dispatch("deletOrders", {
          id: item._id,
          cb: function(data) {
            self.$Message.warning({ message: data });
          }
        });
      });
    }
  }
};
</script>


