import Vue from 'vue'
import Vuex from 'vuex'

import global from './global/state'
import users from './user/state'
import city from './city/state'
import goods from './goods/state'
import address from './address/state'

Vue.use(Vuex)

const store = new Vuex.Store({    
    modules: {
    	users,
 		city,
 		goods,
 		global,
 		address
 	},

})

export default store


