import Vue from 'vue'
import Router from 'vue-router'

import BackendHome from '@/pages/backend-home'
import BackendLogin from '@/pages/backend-login'
import BackendDetailsAdd from '@/pages/backend-details-add'

import FrontArticleTabs from '@/pages/front-article-tabs'
import FrontUserCenter from '@/pages/front-user-center'
import FrontUserAddress from '@/pages/front-user-address'
import FrontChoseAddress from '@/pages/front-chose-address'


import Cart from '@/pages/cart'
import Home from '@/pages/home'
import cookies from 'js-cookie'

const inBrowser = typeof window !== 'undefined'

Vue.use(Router)


const guardRoute = (to, from, next) => {
    var token = cookies.get('b_user') || !inBrowser
    if (!token) {
        next('/backend/login')
    } else {
        next()
    }
}

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/backend/',
      name: '后台登陆',
      component: BackendLogin
    },
    {
      path: '/backend/login',
      name: '后台登陆',
      component: BackendLogin
    },
    {
      path: '/backend/home',
      name: '后台主页',
      component: BackendHome,
      beforeEnter: guardRoute 
    },
    {
      path: '/backend/details',
      name: '后台主页',
      component: BackendDetailsAdd,
      beforeEnter: guardRoute 
    },
    {
      path: '/',
      name: '首页',
      component: Home
    },
    {
      path: '/article/:id',
      name: '文章',
      component: FrontArticleTabs
    },
    {
      path: '/user',
      name: '用户中心',
      component: FrontUserCenter
    },
    {
      path: '/user/address',
      name: '添加新地址',
      component: FrontUserAddress
    },
    {
      path: '/user/address/select',
      name: '选择收件地址',
      component: FrontChoseAddress
    },
    {
      path: '/cart',
      name: '购物车',
      component: Cart
    }
  ]
})



