import { UERSISERT, ADMINLOGIN, CODETOPNG, USERORDERS, GETALLORDERSLIST, DELETORDERSBYID, COMMENTINSERT } from './type'
const mutations = {
    [CODETOPNG](state, data) {
        if (data) {
            state.cpng = data
            data.cb(state);
            //return Object.assign({}, state, {Notifi:data});
        }
    },
    [UERSISERT](state, data) {
        if (data) {
            state.usersNotifi = data;

            //return Object.assign({}, state, {Notifi:data});
        }
    },
    [ADMINLOGIN](state, data) {
        if (data) {
            state.usersNotifi = data;
            state.userName = '';
            state.userPwd = '';
            state.email = '';
            //return Object.assign({}, state, {Notifi:data});
        }
    },
    [USERORDERS](state, data) {
        if (data) {
            state.Notifi = data.message;
        }
    },
    [GETALLORDERSLIST](state, data) {
        if (data) {
            state.data = data.data;
        }
    },
    [DELETORDERSBYID](state, data) {
        if (data) {
            state.Notifi = data.message;
        }
    },
    [COMMENTINSERT](state, data) {
        if (data) { 
            state.Notifi = data.message;
            state.data = data.data;
            state.code = data.code;
        }
    }
}
export default mutations


