<template>
 <div class="goods-type-add">
    
 	 <div class="goodsType">
 	     <at-input v-model="goodsTypeName" placeholder="输入商品类型"></at-input>
 	 </div>
 	
 	 <div class="btn-submit mt10">
 	 	<at-button type="primary" @click="createGoodType">创建商品类型</at-button>
	
 	 </div>
	 <div class="goods-type-del mt10">
		 <div class="goodsDle">
			<el-select @change="changType" v-model="typeValue" style="width:100%" placeholder="选择类别">
				<el-option
				v-for="(item,index) in tyeList"
				:key="index"
				:label="item.type"
				:value="item.type">
				</el-option>
			</el-select>
		 </div>
		 
	  
	 </div>
	 <div class="btn-submit mt10"><at-button type="primary" @click="deletGoodType">删除商品类型</at-button></div>

 </div> 
</template>
<script>
export default {
  props: [""],
  data() {
    return {
      goodsTypeName: "",
      tyeList: [],
      typeValue: '',
      _type: ""
    };
  },
  components: {},
  created() {
    let self = this;
    this.$store.dispatch("getGoodType", {
      cb: function(data) {
        if (data.data.body.code === 200) {
          var arr = [];
          for (var i = 0; i < data.data.body.data.list.length; i++) {
            arr[i] = data.data.body.data.list[i];
          }
		  self.tyeList = arr;


          //self.$refs.atSelect.appendChild('<br>');
        }
      }
    });
  },
  methods: {
    createGoodType() {
      var self = this;
      this.$store.dispatch("createGoodType", {
        goodsTypeName: this.goodsTypeName,
        cb: function(notif) {
          self.$Message.warning({ message: notif.data.data.message });
        }
      });
    },
    deletGoodType() {
	  let self = this;
	  this.$store.dispatch("deletGoodType", {
        type: this._type,
        cb: function(notif) {
          self.$Message.warning({ message: notif.data.data.message });
        }
      });
	},
	changType(v){
		this._type = v;
	}
  }
};
</script>
<style>
.goods-type-add {
  padding: 10px 20px 0;
}
</style>


