import { NOTIFI, GETGOODSLIST, INSERTADSPIC, GETADSPICTURE,DELETADSPICTURE,UPDATEADSPICTURE ,INSERTARTDETAILS} from './type'
const mutations = {
    [NOTIFI](state, data) {
        if (data) {
            state.Notifi = data
            data.cb(state.Notifi);
            //return Object.assign({}, state, {Notifi:data});
        }
    },
    [GETGOODSLIST](state, data) {
        if (data) {
            //state.tableData = data.data.data.data.list;
            //console.log(state)
            //console.log('eee',data.data.data.data.list)
            data.cb(data.data);
            //return Object.assign({}, state, {Notifi:data});
        }
    },
    [INSERTADSPIC](state, data) {
        if (data) { 
            state.data = data.body.data
            state.Notifi = data.body.message
        }
    },
    [GETADSPICTURE](state, data) {
        if (data) {
            state.Notifi = data.body.message
            state.adsPic = data.body.data
        }
    },
    [DELETADSPICTURE](state, data) {
        if (data) {
            state.Notifi = data.body.message
        }
    },
    [UPDATEADSPICTURE](state, data) {
        if (data) {
            state.Notifi = data.body.message
        }
    },
    [INSERTARTDETAILS](state, data) {
        if (data) {
            state.Notifi = data.body.message
            if(data.data){
                state.data =  data.body.data
            }
        }
    }
}
export default mutations


