<template>
<div class="front-user-center">
	<div class="front-user-center-centent">
		<div class="front-user-header">
			 <div class="front-user-photos">
			 	<div class="i-m-c">
			 	    <img src="../assets/images/uc_2.png" alt="">
			 	</div>
			 	<div class="sin-in-out">
			 		<a href="javascript:;"  @click="LoginShow=true;" v-if="(user.name==undefined) ? true : false">登录/注册</a>
			 		<div class="users" v-if="(user.name==undefined) ? false : true">{{user.name}} <span class="ext" @click="ESC">退出</span></div>
			 	</div>
			 </div>
		</div>
	</div>
	<LRModel :LoginShow="LoginShow" :RegShow="RegShow" v-on:ChangeLoginShow="ChangeLoginShow"></LRModel>
	<tfooter></tfooter>
</div>
</template>
<script>
	import { tfooter,LRModel } from '../components/'
	import cookies from 'js-cookie'
    export default {
		data(){
			return {
			   user : {},
               LoginShow : false,
               RegShow : false,
               
			}
		},
		created(){
			
			if(cookies.get('f_username')){
                this.user.name = cookies.get('f_username')
			}
			
		},
		methods : {
			ESC(){
				cookies.remove('f_user');
				cookies.remove('f_userid');
				cookies.remove('f_username');
				this.user = {};
			},
			ChangeLoginShow(a,b,u){
				this.LoginShow = a;
				this.RegShow = b
				if(u){
					this.user.name = u
				}
			}
		},
		components: {
	       tfooter,
           LRModel
		},
	}
</script>
<style>
div.ii{ height: 32px; }

.ext{ margin-left: 10px; }

.front-user-center{ display: flex;  flex-direction: column; width: 100%; height: 100%; background: #fff; }
.front-user-center-centent{ flex: 1;-webkit-overflow-scrolling : touch; overflow: auto; }
.front-user-header{ height: 200px; padding: 20px 0 0 0; background: url(../assets/images/uc_1.jpg); }
.front-user-photos{ margin: 0 auto; width: 100px; height: 100px; border-radius: 50%; background: #828282; }
.front-user-photos .i-m-c{ width: 100px; height: 100px; border-radius: 50%; overflow: hidden; }
.front-user-photos .i-m-c img{ width: 100%; }
.front-user-photos .sin-in-out{ text-align: center; color: #fff; margin-top: 15px; }
.front-user-photos .sin-in-out a{ color: #fff; }
</style>


