<template>
  <div class="article-tabs">
    <div class="article-tabs-cap">
    <i class="el-icon-arrow-left bk" @click="goBack"></i>
    <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="商品" name="first"><FrontGoodsArticle></FrontGoodsArticle></el-tab-pane>
        <el-tab-pane label="详情" name="second"><FrontArticleDetails></FrontArticleDetails></el-tab-pane>
        <el-tab-pane label="评论" name="three"><FrontArticleComment></FrontArticleComment></el-tab-pane>
    </el-tabs>
    </div>
    <tfooter></tfooter>
  </div>
</template>
<script>
import { tfooter } from "../components/";
import FrontGoodsArticle from "./front-goods-article";
import FrontArticleDetails from "./front-article-details";
import FrontArticleComment from "./front-article-comment";
export default {
  data() {
    return {
      activeName: "first"
    };
  },
  methods: {
    handleClick(tab, event) {

    },
    goBack() { 
      window.history.go(-1);
    }
  },
  components: {
    FrontGoodsArticle,
    FrontArticleDetails,
    FrontArticleComment,
    tfooter
  }
};
</script>
<style>
.article-tabs {
  height: 100%;
  position: relative;
  background: #ffffff;
}
.article-tabs-cap {
  height: -webkit-calc(100% - 47px);
}
.article-tabs-cap i.bk {
  position: absolute;
  font-size: 18px;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 38px;
  height: 42px;
  line-height: 42px;
  display: block;
  text-align: center;
  z-index: 10;
}
.article-tabs .el-tabs__header {
  margin-bottom: 0;
}
.article-tabs .el-tabs {
  height: 100%;
}
.article-tabs .el-tabs__content {
  height: -webkit-calc(100% - 43px);
  -webkit-overflow-scrolling: touch;
  overflow: auto;
}

.article-tabs .el-tabs__nav {
  width: 180px;
  float: none;
  margin: 0 auto;
}
.article-tabs .el-tabs__nav .el-tabs__item {
  color: #333;
}
.article-tabs .el-tabs__nav .el-tabs__active-bar {
  background: #333;
}
</style>



