import GoodsMutations from './mutations'
import GoodsAcions from './actions'
const goods = {
	state: {
		typeValue : "",
                goodsName :"",
                goodsDesc : "",
                goodsPrice : 0,
                imageSrc : {
                	url : '',
                	text : '',
                	show : false
                },
                Notifi : {}
	},
	actions: GoodsAcions,
	mutations: GoodsMutations
}

export default goods


