<template>
<div class="user-center">
	 <el-tabs v-model="TabIndex" type="card" @tab-click="tabClickHandle">
	  <el-tab-pane
	    v-for="(item, index) in TabsData"
	    :key="item.name"
	    :label="item.title"
	    :name="item.name"
	  >
	    <!-- <tabcomponent :index="index" :childIndex="index" :component="item.component"></tabcomponent> -->
      <div class="manage-chlid" :is="item.component"></div>
	  </el-tab-pane>
	</el-tabs>
</div>
</template>
<script>
import userCreate from "./backend-user-create";
export default {
  data() {
    return {
      TabIndex: "0",
      TabsData: [
        {
          title: "添加用户",
          name: "0",
          component: userCreate
        },
        {
          title: "用户更改",
          name: "1",
          component: ""
        },
        {
          title: "删除用户",
          name: "2",
          component: ""
        }
      ]
    };
  },
  create() {},
  components: {},
  methods: {
    tabClickHandle(tab, event) {}
  }
};
</script>
<style>
.user-center {
  margin: 0 auto;
  height: 100%;
}
.user-center .el-tabs__nav {
  padding: 0 15px;
}
.user-center .el-tabs__nav .el-tabs__item {
  font-size: 12px;
}
</style>


