import { ADDIDTOCART, DELCARTS } from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'
import Vue from 'vue'

const actions = {
    async ['addToCart']({ commit, state }, config) {
        if (config) {
            const data = await api.addToCart(config)
            console.log('sv', data)
            commit(ADDIDTOCART, data);
            config.cb(state)
        }
    },
    async ['getCartId']({ commit, state }, config) {
        if (config) {
            const data = await api.getCartId(config)
            const obj = {
                data,
                cb: config.cb
            }
            if (data) {
                commit(ADDIDTOCART, obj);
            }
        }
    },
    async ['delCartById']({ commit, state }, config) {
        const data = await api.delCartById(config)
        commit(DELCARTS, data);
        config.cb(state)
    },
    async ['RemovCartByTimeout']({ commit, state }, config) {
        const data = await api.RemovCartByTimeout(config)
        commit(DELCARTS, data);
        config.cb(state)
    }
    
}

export default actions


