<template>
<div class="backend-login">
	<div class="header">
		后台管理系统
	</div>
	<div class="backend-login-main">
		<div class="user-name">
	 	     <at-input v-model="userName" placeholder="输入用户名称"></at-input>
	 	 </div>
	 	 <div class="user-name mt10">
	 	     <at-input v-model="userPwd" type="password" placeholder="输入用户密码"></at-input>
	 	 </div>
	 	 <div class="btn-submit mt10">
	 	 	<at-button type="primary" @click="AdminUserLogin">用户登陆</at-button>
	 	 </div>
 	 </div>
</div>	
</template>
<script>
    import cookies from 'js-cookie'
	export default {
		data(){
			return {
	           userName : '',
	           userPwd : '' 
			}
		},
		created(){
			if(cookies.get('b_user')){
                  this.$router.push({ path: '/backend/home' })
			}
		},
		methods :{
			AdminUserLogin(){
				var self = this;
				
                this.$store.dispatch('adminUserLoginAsync',{
    	    		userName : self.userName,
    	    		userPwd : self.userPwd,
    	    		cb : function () {
    	    			self.$Message.warning({ message: self.$store.state.users.usersNotifi.message ,duration: 800});
    	    			if(self.$store.state.users.usersNotifi.message == "登录成功"){
	    	    			self.$router.push({ path: '/backend/home' })
    	    			}
    	    		}
    	    	})
			}
		}
	}
</script>
<style>
    .mt10{ margin-top: 10px; }
	.backend-login{ padding: 0px 0 0; background: #fff; height: 100%; }
	.backend-login .header{ font-size: 16px; height: 42px; line-height: 42px; text-align: center; color: #333; border-bottom: 1px solid #ededed; margin-bottom: 20px;}
	.backend-login-main{ padding: 0 20px; }
</style>


