import { ADDRESSINSERT , GETADDRESS, ADDRESSSELCTED } from './type'
const mutations = {
    [ADDRESSINSERT](state, data) { 
        if (data) {
            state.Notifi=data.data;
            data.cb(state.Notifi)
        }
    },
    [GETADDRESS](state, data) {
        if (data) {
            state.Notifi = data.data;
            data.cb(state.Notifi)
        }
    },
    [ADDRESSSELCTED](state, data) {
        if (data) {
            state.Notifi = data.data;
            data.cb(state.Notifi)
        }
    }
}
export default mutations


