<template>
  <swiper :options="swiperOption">
    <swiper-slide v-for="(item, index) in imgsData" :key="index"><a :href="item.link"><img :src="item.pictureUrl"></a></swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>

</template>

<script>
require("swiper/dist/css/swiper.css");
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  data() {
    return {
      imgsData: [],
      swiperOption: {
        autoplay: 3000,
        initialSlide: 1,
        loop: true,
        pagination: ".swiper-pagination",
        onSlideChangeEnd: swiper => {
          //console.log("onSlideChangeEnd", swiper.realIndex);
        }
      }
    };
  },
  created() {
    var self = this;
    this.$store.dispatch("getAdsPictureList", {
      cb: function(data) {
        let AdsPicData = data.adsPic; 
        AdsPicData.forEach(function(item, i) {
          if (item.show) {
            self.imgsData.push(item);
          }
        });
        self.imgsData = self.imgsData.sort(self.compare("sort"));
        
      }
    });
  },
  methods: {
    compare(property) {
      return function(a, b) {
        var value1 = a[property];
        var value2 = b[property];
        return value1 - value2;
      };
    }
  },
  mounted() {},
  components: {
    swiper,
    swiperSlide
  }
};
</script>

<style>
img {
  width: 100%;
  height: auto;
}
#swiper .swiper-pagination-bullet {
  background: #e5e8e8;
  opacity: 1;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #007aff;
}
.swiper-pagination-bullet {
  background: #fff;
  opacity: 0.8;
}
</style>


