<template>
  <div class="comment-list">
      <div class="comment pb150">
			<div class="c-h">
				<span class="fl">评论(<em>{{count}}</em>)</span>
				<a class="fr" href="javascript:;"></a>
			</div>
			<div class="comment-list-scroll" ref="scroll">
				<div class="comment-list-item" :key="k" v-for="(item, k) in  comments">
					<div class="comment-item-info">
						<div class="comment-item-data fr">{{item.creat_date}}</div>
						<div class="comment-item-author fl">{{item.user.replay ? `${item.user.from.username} 回复了${item.user.replay.user.username}：` : item.user.from.username}}</div>
					</div>
					<div class="comment-item-content">
						{{item.content}}
					</div>
          
          <el-button class="replay" type="info" @click="replayUser(item.user.from)" size="mini" round>回复</el-button>
				</div>
				
        
			</div>
		</div>
    <div class="pucl">
        <!-- <div class="xx"><label class="fl">评论</label> <div class="full-star fl"><div class="score-start" style="width: 87%;"></div></div></div> -->
        <div class="pub-line"></div>
        <textarea class="pub" ref="content"></textarea>
        <el-button class="add-to-pub" type="info" @click="putComment">发布</el-button>
    </div>
    <LRModel :LoginShow="LoginShow" :RegShow="RegShow" v-on:ChangeLoginShow="ChangeLoginShow"></LRModel>
  </div>
</template>
<script>
import cookies from "js-cookie";
import { tfooter, LRModel } from "../components/";

export default {
  data() {
    return {
      LoginShow: false,
      RegShow: false,
      comments: [],
      count: 0,
      replay: {},
      CH: 0,
      SH: 0,
      OP: false,
      N: true,
      page: 1
    };
  },
  created() {},
  methods: {
    putComment() {
      let uid = cookies.get("f_userid"),
        self = this;
      let gid = this.$route.path.replace(/\/article\//, "");
      let content = this.$refs.content.value;
      if (uid) {
        this.$store.dispatch("CommentInsert", {
          uid: {
            from: uid,
            replay: self.replay
          },
          gid: gid,
          content: content,
          cb: function(data) {
            self.$Message.warning({ message: data.Notifi });

            if (data.code == 200) {
              console.log("aa", data.data.user);
              let uid = data.data.user.from;
              self.$nextTick(() => {
                data.data.user.from = {
                  _id: uid,
                  username: cookies.get("f_username")
                };
                if (data.data.user.replay) {
                  data.data.user.replay.user = {
                    username: self.replay.user
                  };
                }
              });

              let datas = [];
              datas.push(data.data);
              self.comments = datas.concat(self.comments);
              self.count++;
              self.replay = {};
            }
          }
        });
      } else {
        this.LoginShow = true;
      }
    },
    ChangeLoginShow(a) {
      this.LoginShow = a;
    },
    replayUser(u) {
      console.log(u);
      this.replay = {
        id: u._id,
        user: u.username
      };
    },
    CommentsPullByPage(pp) {
      let self = this;
      let gid = this.$route.path.replace(/\/article\//, "");
      let limit = 3;
      this.$store.dispatch("CommentsPull", {
        gid: gid,
        limit: limit,
        page: self.page,
        cb: function(data) {
          self.comments = self.comments.concat(data.data.list);
          self.count = data.data.total;
          self.$nextTick(() => {
            self.CH = pp.clientHeight;
            self.SH = pp.scrollHeight;
            self.OP = false;
            if (data.data.hasNext) {
              self.N = true;
            } else {
              self.N = false;
            }
          });
        }
      });
    }
  },
  mounted() {
    let self = this;
    let gid = this.$route.path.replace(/\/article\//, "");
    let limit = 5;
    let scrolldiv = self.$el.parentNode.parentNode;
    this.$store.dispatch("CommentsPull", {
      gid: gid,
      limit: limit,
      page: self.page,
      cb: function(data) {
        self.comments = data.data.list;
        self.count = data.data.list.lenth;

        self.CH = scrolldiv.clientHeight;
        self.SH = scrolldiv.scrollHeight;
      }
    });
    this.$nextTick(() => {
      self.CH = scrolldiv.clientHeight;
      self.SH = scrolldiv.scrollHeight;
      scrolldiv.addEventListener("scroll", function() {
        let t = this.scrollTop;
        console.log(t > self.SH - self.CH - 20 && !self.OP && self.N);
        if (t > self.SH - self.CH - 20 && !self.OP && self.N) {
          self.OP = true;
          self.page++;
          self.CommentsPullByPage(scrolldiv);
        }
      });
    });
  },
  components: {
    tfooter,
    LRModel
  }
};
</script>
<style>
.comment-list {
  height: 100%;
}
.comment-list .full-star {
  margin: 10px 0 10px 0;
}
.pucl {
  position: fixed;
  bottom: 47px;
  width: 100%;
  background: #fff;
  padding: 15px 0;
}
.pucl .xx {
  height: 36px;
  line-height: 36px;
}
.pucl .xx label {
  margin-left: 12px;
  margin-right: 10px;
}
.pucl .pub {
  display: block;
  width: 94%;
  height: 50px;
  margin: 0 auto;
  border: 1px solid #f1f1f1;
  border-radius: 5px;
  padding: 5px 5px;
}
.comment-list .add-to-pub {
  margin: 10px 0 0 10px;
}
.pucl .pub-line {
  background: #f2f2f2;
  height: 10px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
}
.comment {
  padding: 0 10px 0;
}
.comment.pb150 {
  padding-bottom: 150px;
}
.comment .c-h {
  height: 46px;
  line-height: 46px;
  color: #81838e;
  border-bottom: 1px solid #f1f1f1;
}
.comment .c-h a {
  color: #81838e;
}
.comment .c-h em {
  font-style: normal;
}

.comment-list-item {
  padding: 0 0 10px 0;
  border-bottom: 1px solid #f1f1f1;
}
.comment-item-data {
  color: #848689;
  font-size: 12px;
}
.comment .replay {
  display: inline;
  padding: 2px 4px;
}
.comment-item-info {
  height: 35px;
  line-height: 35px;
  color: #f23030;
}
.full-star {
  margin-top: 10px;
  width: 70px;
  height: 14px;
  line-height: 20px;
  background-size: auto 100%;
  color: #f15353;
  background-image: url("../assets/images/nostart.svg");
}
.score-start {
  width: 100%;
  height: 100%;
  background-size: auto 100%;
  background-image: url("../assets/images/star.svg");
}
.full-star .score {
  font-size: 12px;
}
.comment-item-author {
  color: #848689;
  font-size: 12px;
}
.comment-item-content {
  color: #333;
  padding-bottom: 5px;
  font-size: 13px;
  line-height: 20px;
}
</style>



