import { ADDIDTOCART, CARTCOUNT, DELCARTS } from './type'
const mutations = {
    [ADDIDTOCART](state, data) {
        if (data) {
            state.data = data
        }
    },
    [CARTCOUNT](state, data) {
        state.count = data
    },
    [DELCARTS](state, data) {
        state.Notifi = data.body.message
    }

}
export default mutations


