<template>
  <section class="wrap bk-h">
        <el-col class="mian cc" :span="24">
           <el-tabs class="main-back-TAB" :span="24" v-model="activeName" @tab-click="TabsChange">
              <el-tab-pane :span="8" label="用户中心" name="first"><UserCnter></UserCnter></el-tab-pane>
              <el-tab-pane :span="8" label="商品管理" name="second"><BackManage></BackManage></el-tab-pane>
              <el-tab-pane :span="8" label="订单管理" name="third"><BackendOrders></BackendOrders></el-tab-pane>
           </el-tabs>
       </el-col>
       <div class="footer-bar"><div class="quit" @click="Quit">退 出</div></div>
  </section>
</template>
<script>
import { mapMutations,mapState } from 'vuex'
import BackManage from './backend-manage'
import UserCnter from './backend-user'
import BackendOrders from './backend-orders'
import cookies from 'js-cookie'

export default {
	data(){
		return {
        activeName: 'first'
		}
	},
  created(){ 
     
  },
  methods: {
      TabsChange(tab, event) {
        
      },
      Quit(){
          cookies.remove('b_user');
          cookies.remove('b_userid');
          cookies.remove('b_username');
          this.$router.push({ path: '/backend/login' })
      }

  },
	components: {
       BackManage,
       UserCnter,
       BackendOrders
	},
}	
</script>
<style>
.wrap{width: 100%; height: 100%; display: flex; flex-direction: column; background: #fff; }
.mian.cc{ flex: 1; }
.main-back-TAB .el-tabs__nav{ width: 100%; display: flex; font-size: 12px; height: 40px; line-height: 40px; border-bottom: 1px solid #ededed; }
.main-back-TAB .el-tabs__nav .el-tabs__item{  flex:1; text-align: center; color: #333; }
.mian{ width: 100%;}
.main-back-TAB{ width: 100%; height: 100%; }
.main-back-TAB .el-tabs__header{ margin-bottom: 10px; }
.main-back-TAB .el-tabs__content{ height: -webkit-calc(100% - 51px); -webkit-overflow-scrolling: touch;overflow: auto;}
.wrap.bk-h .footer-bar{ width: 100%; height: 42px; line-height: 42px; color: #333333; border-top: 1px solid #ededed; }
.quit{ padding: 0 20px; float: right; }

</style>


