<template>
<div class="manage">
	 <el-tabs v-model="TabIndex" type="card" @tab-click="tabClickHandle">
	  <el-tab-pane
	    v-for="(item, index) in TabsData"
	    :key="item.name"
	    :label="item.title"
	    :name="item.name"
	  >
	    <!-- <tabcomponent :index="index" :childIndex="index" :component="item.component"></tabcomponent> -->
      <div class="manage-chlid" :is="item.component"></div>
	  </el-tab-pane>
	</el-tabs>
</div>
</template>
<script>
   
  import backendgoodsadd from './backend-goods-add'
  import backendtypeadd from './backend-type-add'
  import backendGoodList from './backend-goods-list'
  import backendAdsPictures from './backend-ads-pictures'

  export default {
    data() {
      return {
        TabIndex : '0',
        TabsData: [{
          title: '增删类型',
          name: '0',
          component : backendtypeadd
        }, {
          title: '添加商品',
          name: '1',
          component : backendgoodsadd
        },
        {
          title: '商品列表',
          name: '2',
          component : backendGoodList
        },
        {
          title: '添加广告图',
          name: '3',
          component : backendAdsPictures
        }],
      }
    },
    create(){
        
    },
    components: {
        
    },
    methods: {
       tabClickHandle(tab, event){
           
       }
    }
  }
</script>
<style>
  .manage{ margin: 0 auto; }
  .manage .el-tabs__nav{ padding: 0 15px; -webkit-overflow-scrolling: touch;
    overflow: auto;}
  .manage .el-tabs__nav .el-tabs__item{  font-size: 12px; height: 38px; line-height: 38px; }
  
</style>


