import { ADDRESSINSERT ,GETADDRESS,ADDRESSSELCTED } from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'
import Vue from 'vue'

const actions = {
    async['addressInsert'] ({commit, state}, config) {
        if (config) { 
            const data = await api.addressInsert(config)
            const obj = {
               data : data,
               cb : config.cb
            }
            commit(ADDRESSINSERT, obj);
        }
    },
    async['getUserAddress'] ({commit, state}, config) {
        if (config) { 
            const data = await api.getUserAddress(config)
            const obj = {
               data : data,
               cb : config.cb
            }
            commit(GETADDRESS, obj);
        }
    },
    async['addressSelect'] ({commit, state}, config) {
        if (config) { 
            const data = await api.addressSelect(config)
            const obj = {
               data : data,
               cb : config.cb
            }
            commit(ADDRESSSELCTED, obj);
        }
    },
    
    
}

export default actions



