import { NOTIFI, GETGOODSLIST, INSERTADSPIC, GETADSPICTURE, DELETADSPICTURE, UPDATEADSPICTURE, INSERTARTDETAILS } from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'
import Vue from 'vue'

const actions = {
    async ['insertGoodAsync']({ commit, state }, config) {
        const data = await api.insertGoodAsync(config)
        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async ['updateGoodAsync']({ commit, state }, config) {
        const data = await api.updateGoodAsync(config)
        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async ['createGoodType']({ commit, state }, config) {
        const data = await api.insertGoodTypeAsync(config)
        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async ['deletGoodType']({ commit, state }, config) {
        const data = await api.deletGoodTypeAsync(config)
        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async ['getGoodType']({ commit, state }, config) {
        const data = await api.getGoodType(config)

        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async ['getGoodByTypeIdAsync']({ commit, state }, config) {
        const data = await api.getGoodByTypeIdAsync(config)
        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(GETGOODSLIST, obj);
        }
    },

    async ['getByIdGroup']({ commit, state }, config) {
        const data = await api.getByIdGroup(config)

        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(GETGOODSLIST, obj);
        }
    },
    async ['getGoodByIdAsync']({ commit, state }, config) {
        const data = await api.getGoodByIdAsync(config)
        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(GETGOODSLIST, obj);
        }
    },
    async ['deleteGoods']({ commit, state }, config) {
        const data = await api.deleteGoodseAsync(config)
        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async ['showGoodsList']({ commit, state }, config) {
        const data = await api.showGoodsListAsync(config)
        const obj = {
            data,
            cb: config.cb
        }
        if (data) {
            commit(GETGOODSLIST, obj);
        }
    },
    async ['inserAdsPicture']({ commit, state }, config) {
        const data = await api.inserAdsPicture(config)

        if (data) {
            commit(INSERTADSPIC, data);
            config.cb(state)
        }
    },
    async ['getAdsPictureList']({ commit, state }, config) {
        const data = await api.getAdsPictureList(config)

        if (data) {
            commit(GETADSPICTURE, data);
            config.cb(state)
        }
    },
    async ['deletAdsPicture']({ commit, state }, config) {
        const data = await api.deletAdsPicture(config)

        if (data) {
            commit(DELETADSPICTURE, data);
            config.cb(state)
        }
    },
    async ['updateAdsPicture']({ commit, state }, config) {
        const data = await api.updateAdsPicture(config)

        if (data) {
            commit(UPDATEADSPICTURE, data);
            config.cb(state)
        }
    },
    async ['insertarticleDetails']({ commit, state }, config) {
        const data = await api.insertarticleDetails(config)

        if (data) {
            commit(INSERTARTDETAILS, data);
            config.cb(state)
        }
    },
    async ['GetarticleDetails']({ commit, state }, config) {
        const data = await api.GetarticleDetails(config)

        if (data) {
            commit(INSERTARTDETAILS, data);
            config.cb(state)
        }
    },
    async ['UpdataarticleDetails']({ commit, state }, config) {
        const data = await api.UpdataarticleDetails(config)

        if (data) {
            commit(INSERTARTDETAILS, data);
            config.cb(state)
        }
    },

}

export default actions


